import React, { Component } from "react";
import { Grid, Cell, List, ListItem, ListItemContent } from "react-mdl";

class Contact extends Component {
  state = {};
  render() {
    return (
      <div className="contact-body">
        <Grid className="contact-grid">
          <Cell col={6}>
            <h2>Jacek Maciejak</h2>
            <img
              src="https://cdn.pixabay.com/photo/2018/08/28/13/29/avatar-3637561_960_720.png"
              alt="Avatar"
              style={{ height: "250px" }}
            />
            <p style={{ width: "70%", margin: "auto", paddingTop: "2em" }}>
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Mollitia
              vero praesentium perferendis dicta, deserunt alias, reprehenderit
              blanditiis saepe minus dignissimos inventore deleniti ipsam vitae
              nesciunt facilis voluptatum harum qui quaerat.
            </p>
          </Cell>
          <Cell col={6}>
            <h2>Contact Me</h2>
            <hr />

            <div className="contact-list">
              <ListItemContent>
                <ListItem>
                  <ListItemContent
                    style={{
                      fontSize: "25px",
                      fontWeight: "bold",
                      fontFamily: "Anton"
                    }}
                  >
                    <i className="fa fa-phone-square" aria-hidden="true" />
                    <span>(123) 123 - 456 - 789</span>
                  </ListItemContent>
                </ListItem>
                <ListItem>
                  <ListItemContent
                    style={{
                      fontSize: "25px",
                      fontWeight: "bold",
                      fontFamily: "Anton"
                    }}
                  >
                    <i className="fa fa-fax" aria-hidden="true" />
                    <span>(123) 123 - 456 - 789</span>
                  </ListItemContent>
                </ListItem>
                <ListItem>
                  <ListItemContent
                    style={{
                      fontSize: "25px",
                      fontWeight: "bold",
                      fontFamily: "Anton"
                    }}
                  >
                    <i className="fa fa-envelope" aria-hidden="true" />
                    <span>someone@example.com</span>
                  </ListItemContent>
                </ListItem>
                <ListItem>
                  <ListItemContent
                    style={{
                      fontSize: "25px",
                      fontWeight: "bold",
                      fontFamily: "Anton"
                    }}
                  >
                    <i className="fa fa-skype" aria-hidden="true" />
                    <span>MySkypeID</span>
                  </ListItemContent>
                </ListItem>
              </ListItemContent>
            </div>
          </Cell>
        </Grid>
      </div>
    );
  }
}

export default Contact;
